@keyframes right-left {
  0% {
    margin-left: 0; }
  100% {
    margin-left: 20px; } }

@font-face {
  font-family: "SF-UI-DisplayBold";
  src: url("../fonts/SF-UI-DisplayRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "SF-UI-DisplayBold";
  src: url("../fonts/SF-UI-DisplayBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "SF-UI-DisplayBold";
  src: url("../fonts/SF-UI-DisplaySemibold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "SF-UI-Display", "Arial", sans-serif;
  box-sizing: border-box;
  background-color: #000000; }

img {
  width: 100%;
  height: auto; }

p {
  margin: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  display: flex;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;
  justify-content: center;
  align-items: center; }

.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #FFFFFF;
  margin: 0 auto;
  max-width: 320px; }

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 320px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.button {
  cursor: pointer;
  background-color: transparent;
  border: none; }

.main-header {
  min-height: 41px;
  padding: 0 23px;
  padding-top: 10px;
  margin-bottom: 20px;
  display: flex; }

.main-header__logo {
  justify-content: flex-start;
  width: auto;
  margin-right: auto;
  transition-duration: 0.3s; }
  .main-header__logo:hover, .main-header__logo:focus {
    transform: scale(1.1); }
  .main-header__logo:active {
    transform: scale(0.9); }

.main-header__navigation {
  display: none; }

.main-header__controls-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 30px;
  min-width: 60px; }

.main-header__controls-item {
  transition-duration: 0.3s; }
  .main-header__controls-item:hover, .main-header__controls-item:focus {
    transform: scale(1.2); }
  .main-header__controls-item:active {
    transform: scale(0.9); }

.main-header__nav-button {
  position: relative;
  font-size: 0;
  width: 41px;
  height: 41px;
  padding: 0;
  transition-duration: 0.3s; }
  .main-header__nav-button:before, .main-header__nav-button:after {
    content: '';
    position: absolute;
    display: block;
    height: 2px;
    background-color: #FFFFFF;
    right: 2px;
    border-radius: 5px; }
  .main-header__nav-button:before {
    top: 35%;
    width: 35px; }
  .main-header__nav-button:after {
    top: 55%;
    width: 25px; }
  .main-header__nav-button:hover, .main-header__nav-button:focus {
    transform: scale(1.2); }
  .main-header__nav-button:active {
    transform: scale(0.9); }

.intro-block {
  min-height: 330px;
  display: flex;
  flex-direction: column;
  background-image: url("../../img/intro_background_mobile.png");
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-size: 100%; }

.intro-block__title {
  margin: 0 23px;
  font-family: "SF-UI-DisplayBold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 25px;
  line-height: 124%; }
  .intro-block__title--highlighted {
    color: #FF970A; }

.intro-block__link {
  position: relative;
  align-self: flex-end;
  justify-content: flex-start;
  margin-top: auto;
  width: 197px;
  min-height: 56px;
  box-sizing: border-box;
  border: 1px solid transparent;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  background: linear-gradient(124.94deg, #000000 -120.88%, rgba(155, 155, 155, 0) 127.52%);
  backdrop-filter: blur(2.55466px);
  background-image: url("../../img/youtube_placeholder_mobile.png");
  background-repeat: no-repeat;
  background-position: right;
  outline: 0; }
  .intro-block__link:hover svg, .intro-block__link:focus svg {
    animation: right-left;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-duration: 0.5s;
    animation-direction: alternate; }
  .intro-block__link:active svg {
    transform: scale(0.9); }

.intro-block__link-text {
  width: 93px;
  font-weight: 400;
  font-size: 11px;
  line-height: 99.5%;
  margin-left: 15px;
  margin-right: 5px; }

.statistics {
  padding: 20px 17px 0 18px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3); }

.statistics__title {
  font-family: "SF-UI-DisplayBold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 44.0427px;
  line-height: 99.5%;
  margin: 0;
  margin-bottom: 7px; }

.statistics__subtitle {
  font-size: 13px;
  line-height: 118%;
  color: #A3A3A3;
  margin-bottom: 25px; }

.statistics__results {
  font-weight: bold;
  font-size: 10px;
  line-height: 99.5%;
  color: #FF970A;
  margin-bottom: 10px; }

.statistics__list {
  display: flex;
  margin-bottom: 28px; }

.statistics__item {
  position: relative;
  flex-grow: 1;
  margin-right: 17px; }
  .statistics__item:last-child {
    margin-right: 0; }

.statistics__item-title {
  margin: 0;
  margin-bottom: 5px;
  font-family: "SF-UI-DisplayBold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 26px;
  line-height: 99.5%; }

.statistics__item-text-wrapper {
  display: flex;
  align-items: flex-end; }

.statistics__item-text {
  font-size: 10px;
  font-weight: normal;
  line-height: 99.5%;
  width: 84%;
  margin-right: auto; }

.statistics__item-popup {
  display: flex;
  align-self: flex-end;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 8.84142px;
  line-height: 99.5%;
  width: 12px;
  height: 12px;
  box-sizing: border-box;
  border-radius: 1.71px;
  backdrop-filter: blur(3.65217px);
  background: linear-gradient(310deg, #000000 -120.88%, rgba(155, 155, 155, 0.3) 127.52%);
  cursor: pointer;
  transition-duration: 0.3s; }
  .statistics__item-popup:hover, .statistics__item-popup:focus {
    transform: scale(1.1); }
  .statistics__item-popup:active {
    transform: scale(0.9); }
  .statistics__item-popup:hover ~ .statistics__popup-text {
    display: block; }

.statistics__popup-text {
  position: absolute;
  top: 0;
  right: 15px;
  display: none;
  width: 50px;
  height: 40px;
  font-size: 10px;
  font-weight: normal;
  line-height: 99.5%;
  border: 1px solid #A3A3A3;
  border-radius: 15px;
  background-color: #FFFFFF;
  color: #000000;
  opacity: 0.9;
  padding: 5px; }
